</template>

<script>
import VideoPlayer from "../components/VideoPlayer.vue"

export default {
  components: {
    VideoPlayer
  },
  data() {
    return {
      isTrailerVisible: false,
    }
  },
  methods: {
    openTrailer() {
      if (!this.isTrailerVisible) {
        this.isTrailerVisible = true
      } else {
        this.isTrailerVisible = false
      }
    }
  },
};
</script>

